import { Button, Grid, makeStyles, Typography } from '@material-ui/core'
import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { approve, decline } from '../redux/actions/profileAction'
const useStyles = makeStyles((theme)=>({
    textfield: {
      margin:'20px'
    
      
     
      
  },
  tab: {
    backgroundColor:'#002884',
    color:'#FFFFFF',
    height:'35px',
  
    '&:hover': {
      backgroundColor: '#fff',
      color: '#000000',
      border:'1px solid #000000',
      height:'35px',
    
  },
  },
     
     
  
   
  }));
const ApproveBtn = ({user}) => {
    const classes = useStyles();
    const [followed, setFollowed] = useState(false)

    const { auth, profile, socket } = useSelector(state => state)
    const dispatch = useDispatch()

    const [load, setLoad] = useState(false)

    useEffect(() => {
        if(auth.user.approved.find(item => item._id === user._id)){
            setFollowed(true)
        }
        return () => setFollowed(false)
    }, [auth.user.approved, user._id])


    const handleFollow =  async () => {
        if(load) return;

        setFollowed(true)
        setLoad(true)
        await dispatch(approve({users: profile.users, user, auth, socket}))
        setLoad(false)
    }

    const handleUnFollow = async () => {
        if(load) return;

        setFollowed(false)
        setLoad(true)
        await dispatch(decline({users: profile.users, user, auth, socket}))
        setLoad(false)
    }

    return (
        <>
        {
            followed
            ? <>
            <Grid 
            container
            alignItems='center'
            justifyContent='center' >
             <Typography style={{marginBottom:'20px'}} align = 'center'>!   {user.gender===' '?'' : ''}              -    --</Typography>
              
            </Grid>
            
            
            
            </>
            : 
            <>
            <Grid 
            container
            alignItems='center'
            justifyContent='center' >
             <Typography style={{marginBottom:'20px'}} align = 'center'> {user.gender===' '?'' : ''}              ,    </Typography>
             <Button disableRipple style={{maxWidth:'600px',}} className={classes.tab}
                 onClick={handleFollow}>
                     <Typography>
                     {user.gender===' '?'' : ''}     
                     </Typography>
               
            </Button>  
            </Grid>
            
            
            
            </>
        }
        
        </>
    )
}

export default ApproveBtn
