import { Button, Card, CardContent, Grid, TextField, Typography } from '@material-ui/core';
import React, { useState } from 'react'
import { Helmet } from 'react-helmet-async';
import { useSelector, useDispatch } from 'react-redux'


import { createApplicant } from '../redux/actions/deleteAction';

const DeleteBiodata = () => {
  const dispatch = useDispatch()
  
  const [fullname, setFullname] = useState('')
  const [mobile, setMobile] = useState('')
  const [email, setEmail] = useState('')
  const [reason, setReason] = useState('')
  const [action, setAction] = useState('')
  const [biodataNumber, setBiodataNumber] = useState('')
  

  

   
   
  const handleSubmit = e => {
    e.preventDefault()
    dispatch(createApplicant({ fullname, mobile, email, reason, action, biodataNumber,}))
}

    return (
      <div className="App"> 
      <Helmet>
         <title>Delete Your Biodata</title>
         <meta name='description' content="  /     "/>
          <link rel='canonical' href='/deleteBiodata'/>
      </Helmet>
      <Typography gutterBottom variant="h5" align="center">
        /    
       </Typography>
      <Grid>
        <Card style={{ maxWidth: 450, padding: "20px 5px", margin: "0 auto" }}>
          <CardContent>
            <Typography gutterBottom variant="body3">
             /      <br/><br/>
              '   <br/><br/>
            .              ,      <br/><br/>
            .                                 <br/><br/>
          </Typography> 
            
            <form onSubmit={handleSubmit}>
              <Grid container spacing={1}>
                <Grid  item xs={12}>
                  <TextField 
                  placeholder="   " 
                  label=" " 
                  variant="outlined" 
                  fullWidth 
                  required
                  id="fullname"
                  name="fullname"
                  value={fullname}
                  onChange={(e) => {
                    setFullname(e.target.value) }} />
                </Grid>

                <Grid item xs ={12}>
                  <TextField 
                  required
                  fullWidth
                  variant="outlined"
                  label="  "
                  placeholder="  "
                  id="biodataNumber"
                  name="biodataNumber"
                  value={biodataNumber}
                  onChange={(e) => {
                    setBiodataNumber(e.target.value) }} 
                   ></TextField>
                </Grid>
                
                <Grid item xs={12}>
                  <TextField 
                  type="number" 
                  placeholder="   " 
                  label=" " 
                  variant="outlined" 
                  fullWidth 
                  required
                  id="mobile"
                  name="mobile"
                  value={mobile}
                  onChange={(e) => {
                    setMobile(e.target.value) }} />
                </Grid>
                <Grid item xs ={12}
               
                >
                  <Typography gutterBottom variant="body2"color="textSecondary" >       </Typography>
                </Grid>
                <Grid item xs={12}>
                  <TextField 
                  type="email" 
                  placeholder=" -  " 
                  label="- " 
                  variant="outlined" 
                  fullWidth 
                  required
                  id="email"
                  name="email"
                  value={email}
                  onChange={(e) => {
                    setEmail(e.target.value) }} />
                </Grid>
                <Grid item xs ={12}
               
                >
                  <Typography gutterBottom variant="body2"color="textSecondary" >   -   </Typography>
                </Grid>
                
                <Grid item xs = {12}>
                  <TextField 
                  required
                  select
                  fullWidth
                  variant="outlined"
                  label="  "
                  id="action"
                  name="action"
                  value={action} 
                  onChange={(e) => {
                    setAction(e.target.value) }} >
                    <option value ="   ">   </option>
                    <option value ="  ">  </option> 
                    
                    </TextField>
                </Grid>
                {action==="   "? <Grid item xs={12}>
                  <TextField 
                  type="email" 
                  multiline
                  minRows={5}
                  label="  " 
                  variant="outlined" 
                  fullWidth 
                  required
                  id="reason"
                  name="reason"
                  value={reason}
                  onChange={(e) => {
                    setReason(e.target.value) }} />
                </Grid> :
                <Grid item xs={12}>
                  <TextField 
                  type="email" 
                  multiline
                  minRows={5}
                  label="  " 
                  variant="outlined" 
                  fullWidth 
                  required
                  id="reason"
                  name="reason"
                  value={reason}
                  onChange={(e) => {
                    setReason(e.target.value) }} />
                </Grid>}
                
                <Grid item xs={12}>
                  <Button style={{marginTop:'20px'}} type="submit" variant="contained" color="primary" fullWidth>Submit</Button>
                </Grid>

              </Grid>
            </form>
          </CardContent>
        </Card>
      </Grid>
    </div>
       
          
     
     
                
    )
}

export default DeleteBiodata
